<template>
    <div id="ranking">
        <keep-alive>
            <div class="table-responsive">
                <table class="table table-striped">
                    <caption>{{ getTrans('caption') }}<br><small>{{ getTrans('sub_caption') }}</small><my-ranking :game="gameId" :config="{trans: config.trans}" :listBig="true" v-if="isLogged"></my-ranking><pagination :lang="lang" :current-page="currentPage" :page-count="pageCount" :size="$mq | mq({ xxs: 1, md: 9, xl: 15 })" @navigate="navigate"></pagination></caption>
                    <thead>
                        <tr>
                            <td>{{ getTrans('rank') }}</td>
                            <td>{{ getTrans('player') }}</td>
                            <td>{{ getTrans('score') }}</td>
                            <td>{{ getTrans('games_played') }}</td>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-if="!rankings">
                            <td colspan="4"><LoadingComp></LoadingComp></td>
                        </tr>
                        <tr v-else v-for="ranking in rankings" :key="ranking.id">
                            <td>{{ ranking.rank }}</td>
                            <td><a :href="urlFor('profile-user', {'id': ranking.id})">{{ ranking.name }}</a></td>
                            <td>{{ ranking.score }}</td>
                            <td>{{ ranking.nbgames }}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </keep-alive>
        <pagination :lang="lang" :current-page="currentPage" :page-count="pageCount" :size="$mq | mq({ xxs: 1, md: 9, xl: 15 })" @navigate="navigate"></pagination>
    </div>
</template>

<script>
import HTTP from '@/httpapi';
import Pagination from '@/components/Pagination';
import Ranks from '@api/ranks';
import GenericMixin from '@mixins/generic';
import MyRanking from '@/components/MyRanking';

export default {
    name: 'ranking',

    mixins: [GenericMixin],

    props: ['config'],

    components: {
        pagination: Pagination,
        'my-ranking': MyRanking
    },

    computed: {
        pageCount () {
            return Math.ceil(this.itemCount / this.itemsPerPage);
        },
    },

    methods: {
        navigate (requestedPage) {
            const t = this;
            Ranks.established(this.gameId, requestedPage, this.itemsPerPage).then((result) => {
                t.rankings = result.data.ranks;
                t.itemCount = result.data.total;

                // Now we check if the requested page is reachable. If it is,
                // then fine, otherwise we go back to the closest possible page
                // (usually the last one).
                const currentPage = Math.max(Math.min(requestedPage, this.pageCount), 1);
                if (currentPage === requestedPage) {
                    t.currentPage = requestedPage;
                } else {
                    t.navigate(currentPage);
                }
            });
        },
    },

    watch: {
        currentPage (page, oldPage) {
            // Sync the last part of the URL with the currently displayed page
            const elements = window.location.pathname.split('/');

            if (elements[elements.length - 1].match(/\d+/) !== null) {
                elements.pop();
            }
            elements.push(page);

            window.history.replaceState({}, '', elements.join('/'));
        }
    },

    mounted () {
        const t = this;
        HTTP.ifInitialized(() => t.navigate(t.currentPage));
    },

    data () {
        return {
            rankings: null,
            gameId: this.config.game_id,
            itemsPerPage: 20,
            currentPage: this.config.page,
            itemCount: 0,
        };
    }
};
</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';

#ranking {
    table caption {
        text-align: center;
    }
}

.table-responsive {
    border-width: 0px;
    a {
        color: map-get($asnet-colors, 'dark-purple');
    }
}

ul.pagination {
    li.previous a,
    li.next a {
        cursor: pointer;
    }
}
</style>



